var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-tab',{attrs:{"title":"Transparency and Accountability in ADMS","active":""}},[_c('h2',[_vm._v("Transparency")]),_c('p',[_vm._v(" Algorithmic systems are frequently referred to as ‘black boxes’ – as instruments into which inputs and outputs are visible, but the precise mechanism of its function is inscrutable. Such ‘black box’ Automated Decision-Making Systems pose structural challenges to democratic ideals of transparency, accountability and participation, and consequently, to public trust in the operation of these systems. It is important to interrogate how these constraints arise and what their consequences are. ")]),_c('div',{},[_c('p',[_vm._v(" Transparency is an essential element of a democratic society. Without adequate information about decisions that affect their lives, people cannot comprehend how these decisions are made, how they may affect them, or how they can participate in, and if possible, change such decisions. Similarly, it is impossible to hold the use of ADMS accountable to any legal standard or guiding principles if the mechanisms by which it functions are unknown. Many ADMS pose challenges to these ideals, both by being technically opaque, as well as due to the institutional opacity which often surrounds these systems. ")])]),_c('p',[_vm._v(" The technical opacity of ADMS is a well-known phenomenon. As explained previously, computer algorithms within ADMS are essentially ‘models’ or abstractions of the decision-making metrics which are employed by human beings. However, in the process of creating these algorithmic models, many aspects of the decision-making logic may be altered. One area where technical opacity arises is from the inability to be able to read or parse the computer programme and understand how this decision-making logic has been altered in its translation to software or code. Given that not everyone is able to understand how the decision-making logic is reflected in source code, even where the source code of a computer programme and algorithm is made available, the logic always be obvious or accessible to users or affected persons. ")]),_c('p',[_vm._v(" The technical opacity of ADMS is a well-known phenomenon. As explained previously, computer algorithms within ADMS are essentially ‘models’ or abstractions of the decision-making metrics which are employed by human beings. However, in the process of creating these algorithmic models, many aspects of the decision-making logic may be altered. One area where technical opacity arises is from the inability to be able to read or parse the computer programme and understand how this decision-making logic has been altered in its translation to software or code."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote1sym","name":"sdfootnote1anc"}},[_c('sup',[_vm._v("1")])])]),_vm._v(" Given that not everyone is able to understand how the decision-making logic is reflected in source code, even where the source code of a computer programme and algorithm is made available, the logic always be obvious or accessible to users or affected persons."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote2sym","name":"sdfootnote2anc"}},[_c('sup',[_vm._v("2")])])])]),_c('p',[_vm._v(" With the increasing scale and complexity of algorithmic tools, which rely on processing hundreds or thousands of inputs through myriad statistical or mathematical processes, the logic of decision-making employed within algorithms becomes even more difficult to interpret, even to the creators of these systems. This may particularly be true for contemporary machine learning practices like image recognition through neural networks, where the algorithmic system can produce accurate outputs, but using metrics which have been ‘learned’ by the system, that are so abstracted from the initial logic of the designers of the algorithm, as to be technically un-interpretable by humans."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote3sym","name":"sdfootnote3anc"}},[_c('sup',[_vm._v("3")])])])]),_c('p',[_vm._v(" Another source of the opacity of ADMS is institutional – caused by the legal and institutional mechanisms which govern the operation of these systems. Many of the ADMS documented here include computational algorithms developed by and procured from private firms, which have deployed resources into the creation of these systems, and have an economic incentive to retain proprietary ownership and rights over these systems. As such, many algorithmic systems are considered the intellectual property of these private firms, which are protected as trade secrets of copyrights of these firms. These laws prevent the databases, algorithms and other associated components of ADMS from being accessed or scrutinised by the public, and often even by government agencies procuring such systems. With governments increasingly outsourcing important infrastructure, including ADMS infrastructure, to private firms, there is correspondingly a shift in the norms of transparency – from public and transparent by default, as recognised under a right to information, to private and protected by default, protected both by laws like trade secrets, as well as by the forms and governance practices of private companies."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote4sym","name":"sdfootnote4anc"}},[_c('sup',[_vm._v("4")])])])]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" With governments increasingly outsourcing important infrastructure, including ADMS infrastructure, to private firms, there is correspondingly a shift in the norms of transparency – from public and transparent by default, as recognised under a right to information, to private and protected by default, protected both by laws like trade secrets, as well as by the forms and governance practices of private companies. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-1'),expression:"'case-study-1'"}],staticClass:"modal-button"},[_vm._v("[Case Study: RTI and Algorithmic Systems]")]),_c('b-modal',{attrs:{"id":"case-study-1","size":"xl","hide-footer":""}},[_c('div',[_c('b-img',{staticStyle:{"width":"100%"},attrs:{"center":"","src":require("../../public/img1.png"),"fluid":"","alt":"RTI and Algorithmic Systems"}}),_c('p',[_vm._v(" [Image: Excerpt from an RTI response by the Mumbai Police, on the Mumbai City Surveillance Project] ")])],1),_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: RTI and Algorithmic Systems")]),_c('p',[_vm._v(" The Right to Information Act in India was a landmark moment which made concrete the constitutional right to information, recognising the right of a democratic public to demand information from the government, and the responsibility of the public agencies to proactively provide information, including information about how policy decisions are taken; as well as providing information to individuals about decisions taken about them. ")]),_c('p',[_vm._v(" Algorithmic systems have denuded the democratic safeguards that laws like the RTI Act provide. While the definition of ‘information’ under the RTI Act is broad enough to cover source code, or algorithmic models, this information is often not provided by claiming exemptions which exist under the RTI Act, such as Section 8(1)(d), which exempts the disclosure of confidential information and intellectual property in some circumstances; or Section 8(1)(a), which exempts the disclosure of information on grounds of the sovereignty and security of the nation. Further, disclosure of components of an ADMS, like the databases on which an algorithmic system operates, may pose risks to other interests like privacy and personal data protection, which are also recognised in the RTI Act under Section 8(1)(j). ")]),_c('p',[_vm._v(" The proliferation of ADMS is systematically incapacitating important democratic norms and values, of which the Right to Information appears to be one casualty. There is an urgent need to reform and bring laws and methods of governmental transparency in line with the use of Automated Decision-Making within government. ")])])])],1),_c('p',[_vm._v(" Transparency is not an end in itself – it is a necessary but not a sufficient means of ensuring democratic and equitable ADMS use. In particular, it is necessary to understand what forms of transparency can lead to better democratic participation and outcomes, and how transparency can be balanced against other legitimate considerations like privacy, or the need to prevent unintended uses of algorithmic systems."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote5sym","name":"sdfootnote5anc"}},[_c('sup',[_vm._v("5")])])])]),_c('h2',[_vm._v("Accountability")]),_c('p',[_vm._v(" An important and related concern to the transparency of ADMS in India is the lack of clear systems of accountability for the failures or harms caused by ADMS. Accountability for ADMS requires attributing responsibility to an actor, and providing recourse to people affected by a particular outcome of an ADMS. ")]),_c('p',[_vm._v(" Algorithmic systems often shift or distribute agency for decisions in ways which have not been previously encountered, and obscures clear lines of responsibility for the outcomes of the system. For example, an algorithm may be designed by one actor, operating on data provided by another, and finally, the ADMS may be used or deployed by a third actor, each of whom would have limited knowledge about the others. Often, this could result in attributing agency and culpability for an outcome of the system to a human actor or institution who did not have control or agency over the decision, or even leading to circumstances where the absence of responsibility implies no accountability or redress for affected persons."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote6sym","name":"sdfootnote6anc"}},[_c('sup',[_vm._v("6")])])])]),_c('p',[_vm._v(" Another reason that ADMS obscures clear accountability is due to the increasing autonomous decision-making capabilities of computer systems – there are many instances where an algorithmic system malfunctions or performs in a manner which could not be reasonably foreseen, making attribution of responsibility difficult. This is particularly true for contemporary machine learning systems which may make non-intuitive decisions, the logic of which would be difficult to comprehend (as explained in the section above). ")]),_c('div',{},[_c('p',[_vm._v(" Accounting for accountability requires providing clear lines of responsibility for the harms or failures caused by ADMS, including establishing clear liability for the damage caused by ADMS, and clear channels of redress to affected persons. ")])]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" Another reason that ADMS obscures clear accountability is due to the increasing autonomous decision-making capabilities of computer systems – there are many instances where an algorithmic system malfunctions or performs in a manner which could not be reasonably foreseen, making attribution of responsibility difficult. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-2'),expression:"'case-study-2'"}],staticClass:"modal-button"},[_vm._v("[Case Study: Aadhaar and Unaccountable Authentication Failures]")]),_c('b-modal',{attrs:{"id":"case-study-2","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: Aadhaar and Unaccountable Authentication Failures")]),_c('p',[_vm._v(" The Government of India’s Aadhaar Unique Identification system consists of various assemblages of algorithms, including algorithmic systems which identify individuals, known as ‘authentication’. Biometric authentication has a documented high failure rate"),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote7sym","name":"sdfootnote7anc"}},[_c('sup',[_vm._v("7")])])]),_vm._v(" and the failure of identification, or incorrect identification can have devastating consequences for an individual – including the denial of essential public services like ration. Biometric systems are essentially probabilistic, which means that there is always a chance of an error in the matching – and that a mathematical algorithmic threshold must be established for what percentage error is acceptable and what is not. Given this error-prone nature of biometric algorithms, and the consequences of its failure, clear accountability becomes particularly essential."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote8sym","name":"sdfootnote8anc"}},[_c('sup',[_vm._v("8")])])])]),_c('p',[_vm._v(" However, this measure of accountability has not been forthcoming from the Government of India or the agency responsible for Aadhaar – the Unique Identification Authority of India (UIDAI). The legislation governing Aadhaar does not specifically detail who is responsible to resolve failures of the biometric matching algorithm, or how such resolution should occur. While some circulars issued by the UIDAI specify that government agencies should incorporate ‘exception handling measures’ in case of biometric failures, it has been observed that these measures are often "),_c('a',{attrs:{"href":"https://uidai.gov.in/about-uidai/legal-framework/circulars/12135-exception-handling-in-public-distribution-services-and-other-welfare-schemes-2.html"}},[_vm._v("not made available.")]),_vm._v(" Moreover, alternatives apart, no clear system of accountability has been framed for the failure of the biometric algorithms."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote9sym","name":"sdfootnote9anc"}},[_c('sup',[_vm._v("9")])])])]),_c('p',[_vm._v(" Aadhaar still remains an outlier, in that its use is governed by a specific legal and regulatory regime, and some measures of accountability like grievance redressal mechanisms exist. Even so, it remains a cautionary example of the 