var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-tab',{attrs:{"title":"ADMS + Discrimination"}},[_c('p',[_vm._v(" Automated Decision Making Systems are often used to generate classifications about individuals or communities, for example, by ascribing certain labels or classifications like ‘fraudulent’, ‘creditworthy’, ‘criminal’ or ‘trustworthy’ to groups that share common attributes. The use of ADMS to classify and rank information and data, can be systematically biased and discriminatory against individuals or communities which possess certain characteristics, for example, along lines of gender, class, caste or ethnicity, which can lead to unjust social outcomes for these groups. This section interrogates how ADMS in India is used to classify among people and populations and how this can lead to systemic and structural discrimination. ")]),_c('div',[_c('b-img',{staticStyle:{"width":"100%","height":"650px","object-fit":"cover"},attrs:{"center":"","src":require("../../public/icon7.png"),"fluid":"","alt":""}})],1),_c('p',[_vm._v(" ADMS can be systematically biased against, or in favour of, groups of people who share particular attributes. Often, these biases reproduce socially-embedded and historical discrimination along lines of gender, caste or ethnicity. Due to the scale at which algorithms used in various ADMS are applied, these biases can quickly become pervasive and socially consequential. However, the sources of discrimination can be unaccounted for or overlooked, and often are obscured due to the challenges of transparency in ADMS, making it difficult to identify or rectify."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote1sym","name":"sdfootnote1anc"}},[_c('sup',[_vm._v("1")])])])]),_c('p',[_vm._v(" Multiple sources of bias can exist within ADMS, which can lead to discriminatory outcomes – from the technical architecture of the algorithmic model, the data on which it operates, or the context in which it is used. For example, the historical biases in the kind of data collected within policing databases and used within the CCTNS system can become embedded within ADMS use in policing more generally. Historical police records over-represent particular communities along lines of class and caste, and an automated system relying upon such data is more likely to over-represent such communities, for example, in making decisions about which areas to police. ")]),_c('p',[_vm._v(" Historical patterns of discrimination are particularly likely to be reproduced in the functioning of machine learning systems. Since these types of algorithmic systems learn patterns from underlying historical data and apply these patterns in making decisions about future behaviours or phenomenon, they are more likely to recognise and reproduce existing inequalities and discrimination, often in a way which is posited as ‘neutral’ or ‘objective’."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote2sym","name":"sdfootnote2anc"}},[_c('sup',[_vm._v("2")])])]),_vm._v(" Processes which have relied upon machine learning, like modern image recognition and facial recognition systems, or ‘predictive policing’ systems have been shown to reproduce such historical biases due to underlying biases present in the datasets upon which they operate. For example, studies of facial recognition technologies have consistently shown how their performance varies according to ethnicity – in part because the images on which they are ‘trained’ are not diverse."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote3sym","name":"sdfootnote3anc"}},[_c('sup',[_vm._v("3")])])])]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-8'),expression:"'case-study-8'"}],staticClass:"modal-button"},[_vm._v("[Case Study: Automated Facial Recognition System and the NIST Standards]")]),_c('b-modal',{attrs:{"id":"case-study-8","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v(" Case Study: Automated Facial Recognition System and the NIST Standards ")]),_c('p',[_vm._v(" In July, 2019, the National Crime Records Bureau began the process of inviting bids for the installation of an ‘Automated Facial Recognition System’ which would connect to policing and law enforcement databases around the country, in order to build a centralised, national facial recognition system. In the procurement documents like the Request for Proposal, the NCRB has relied upon specific technical standards in order to indicate the proposed ‘efficiency’ and reliability of the software. In particular, the NCRB has relied on the test for the accuracy of FRT conducted by the National Institute of Standards and Technologies, along with technical demonstrations of how ‘accurate’ the technology is, based on data provided by the NCRB. ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" Accuracy, however, is a highly contingent metric. As per the NIST itself, facial recognition algorithms perform differently across different demographics – along lines of gender and ethnicity. Additionally, these systems perform very differently under ‘test conditions’ as against when they are deployed in real-world scenarios. ")]),_c('p',[_vm._v(" Accuracy, however, is a highly contingent metric. As per the NIST itself, facial recognition algorithms perform differently across different demographics – along lines of gender and ethnicity. Additionally, these systems perform very differently under ‘test conditions’ as against when they are deployed in real-world scenarios. The NIST itself recognises this and creates distinct benchmarks for its Facial Recognition Vendor Test (FRVT), relied upon by the NCRB. The ‘accuracy’ ultimately depends on the underlying data presented to the system, as well as the new data on which it is expected to operate. Therefore, a single test or number, such as the one indicated under the RFP, is insufficient to understand whether the system will function ‘correctly’ across demographics. A testing process which does not account for potential discrimination could, therefore, falsely categorise a system as being ‘accurate’ based only on its performance on a particular group. ")]),_c('div',[_c('b-img',{staticStyle:{"width":"100%","height":"650px","object-fit":"cover"},attrs:{"center":"","src":require("../../public/icon6.png"),"fluid":"","alt":"surveillance and profiling harms"}})],1),_c('p',[_vm._v(" Discriminatory FRT use in law enforcement can have severe consequences – not only for individuals who may be falsely flagged and subject to invasive policing procedures, but particularly when reproduced at a social level, they can replicate, enhance, and potentially and perversely justify discriminatory police practice against minorities and marginalised populations. ")])])])],1),_c('p',[_vm._v(" Another source of discrimination is in the modelling of the algorithmic system and the biases inherent in the task of selecting the inputs and outputs of the algorithmic system. The choice of selecting particular data as relevant factors in classification or prediction is key to the task of algorithmic modelling, and can embed assumptions which lead to discrimination. For example, a software known as "),_c('a',{attrs:{"href":"https://www.propublica.org/article/machine-bias-risk-assessments-in-criminal-sentencing"}},[_vm._v("COMPAS")]),_vm._v(", used in the USA to determine risk for releasing undertrial prisoners on bail, had been shown to systematically discriminate against people of colour. One possible reason for this discrimination could be the factors which were chosen to indicate ‘risk of re-offending’ – including prior arrests and arrests of close families or friends. These data or ‘features’ were more likely to occur for people of colour, and consequently, these factors could have influenced the algorithm to systematically indicate higher risk for these groups.Similarly, a study of the Delhi Police’s ‘Crime Mapping’ software C-MAPS indicated that the filters used to identify areas for policing or crime hotspots are classified using filters for immigrant settlements or minority areas. Such biases may also be embedded within algorithmic design unintentionally, through the lack of testing or considerations of diverse populations. For example, the Aadhar systems fingerprinting and biometric pattern matching algorithm has been shown to underperform on particular demographics, including based on age and gender. Examples like this also show how proxy characteristics – or indirect discrimination – can be built into algorithmic systems, even if the algorithm does not directly consider protected attributes like race or gender as an input in making decisions. ")]),_c('p',[_vm._v(" The various sources of biases inherent withing ADMS, coupled with the failures of accountability and transparency outlined in this toolkit, make discrimination within the large technological systems used by public agencies difficult to uncover and challenge. Moving ahead with governance through ADMS without critically reflecting on how these challenges of discrimination can be resolved is antithetical to principles of substantive equality and non-discrimination that we value. ")]),_c('p',[_vm._v(" Another source of discrimination is in the modelling of the algorithmic system and the biases inherent in the task of selecting the inputs and outputs of the algorithmic system. The choice of selecting particular data as relevant factors in classification or prediction is key to the task of algorithmic modelling, and can embed assumptions which lead to discrimination. For example, a software known as "),_c('a',{attrs:{"href":"https://www.propublica.org/article/machine-bias-risk-assessments-in-criminal-sentencing"}},[_vm._v("COMPAS")]),_vm._v(", used in the USA to determine risk for releasing undertrial prisoners on bail, had been shown to systematically discriminate against people of colour. One possible reason for this discrimination could be the factors which were chosen to indicate ‘risk of re-offending’ – including prior arrests and arrests of close families or friends. These data or ‘features’ were more likely to occur for people of colour, and consequently, these factors could have influenced the algorithm to systematically indicate higher risk for these groups. Similarly, a study of the Delhi Police’s ‘Crime Mapping’ software C-MAPS indicated that the filters used to identify areas for policing or crime hotspots are classified using filters for immigrant settlements or minority areas."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote4sym","name":"sdfootnote4anc"}},[_c('sup',[_vm._v("4")])])]),_vm._v(" Such biases may also be embedded within algorithmic design unintentionally, through the lack of testing or considerations of diverse populations."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote5sym","name":"sdfootnote5anc"}},[_c('sup',[_vm._v("5")])])]),_vm._v(" For example, the Aadhar systems fingerprinting and biometric pattern matching algorithm has been shown to underperform on particular demographics, including based on age and gender."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote6sym","name":"sdfootnote6anc"}},[_c('sup',[_vm._v("6")])])]),_vm._v("Examples like this also show how proxy characteristics – or indirect discrimination – can be built into algorithmic systems, even if the algorithm does not directly consider protected attributes like race or gender as an input in making decisions. ")]),_c('p',[_vm._v(" The various sources of biases inherent withing ADMS, coupled with the failures of accountability and transparency outlined in this toolkit, make discrimination within the large technological systems used by public agencies difficult to uncover and challenge. Moving ahead with governance through ADMS without critically reflecting on how these challenges of discrimination can be resolved is antithetical to principles of substantive equality and non-discrimination that we value. ")]),_c('div',{staticClass:"footnotes"},[_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote1anc","name":"sdfootnote1sym"}},[_vm._v("1")]),_vm._v(" Barocas S and Selbst AD, ‘Big Data’s Disparate Impact’, "),_c('a',{attrs:{"href":"https://papers.ssrn.com/sol3/papers.cfm?abstract_id=2477899#"}},[_vm._v("104 California Law Review 671 (2016)")]),_vm._v(". ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote2anc","name":"sdfootnote2sym"}},[_vm._v("2")]),_vm._v(" Chouldechova, A., ‘Fair prediction with disparate impact: A study of bias in recidivism prediction instruments’, 5(2) Big Data, 153–163, 2017. ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote3anc","name":"sdfootnote3sym"}},[_vm._v("3")]),_vm._v(" Buolamwini J and Gebru T, ‘Gender Shades: Intersectional Accuracy Disparities in Commercial Gender Classification’, Proceedings of the 1st Conference on Fairness, Accountability and Transparency, PMLR 81:77-91, 2018 ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote4anc","name":"sdfootnote4sym"}},[_vm._v("4")]),_vm._v(" Narayan, S, and Marda, V, ‘Data in New Delhi’s Predictive Policing System, Proceedings of the 2020 Conference on Fairness, Accountability, and Transparency, <"),_c('a',{attrs:{"href":"https://dl.acm.org/doi/abs/10.1145/3351095.3372865"}},[_c('u',[_vm._v("https://dl.acm.org/doi/abs/10.1145/3351095.3372865")])]),_vm._v("> ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote5anc","name":"sdfootnote5sym"}},[_vm._v("5")]),_vm._v(" Drozdowski P and others, ‘Demographic Bias in Biometrics: A Survey on an Emerging Challenge’ [2020] "),_c('a',{attrs:{"href":"https://ieeexplore.ieee.org/xpl/RecentIssue.jsp?punumber=8566059"}},[_vm._v("IEEE Transactions on Technology and Society")]),_vm._v(" ( Volume: 1, "),_c('a',{attrs:{"href":"https://ieeexplore.ieee.org/xpl/tocresult.jsp?isnumber=9107511"}},[_vm._v("Issue: 2")]),_vm._v(", June 2020) ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote6anc",