var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-tab',{attrs:{"title":"ADMS + Surveillance and Profiling"}},[_c('div',[_c('b-img',{staticStyle:{"width":"100%","height":"650px","object-fit":"cover"},attrs:{"center":"","src":require("../../public/pimg9.png"),"fluid":"","alt":"surveillance and profiling harms"}})],1),_c('p',[_vm._v(" Automated Decision-Making Systems are becoming integral to the proliferation of surveillance enabled through information technology – a method of observing, knowing and governing individuals and populations through information collected about them. Many of the ADMS technologies documented in this project are explicitly used for surveillance – to govern and police populations to reduce risk and undesired behaviour. This section examines how ADMS in India is used to further surveillance and profiling by government agencies. ")]),_c('p',[_vm._v(" ADMS requires the behaviours, traits and attributes of individuals and communities to be abstracted as data, in particular ways which are suitable for algorithmic computation. Contemporary ADMS like machine learning technologies require increasingly vast amounts of information in order to draw usable relationships between them through computation. The necessity for huge volumes of data is routinely invoked as a critical first step for the development of Artificial Intelligence and related technologies."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote1sym","name":"sdfootnote1anc"}},[_c('sup',[_vm._v("1")])])]),_vm._v(" ADMS and ‘AI’ are therefore providing the imperative for amassing vast amounts of data in ways which allow for their algorithmic computation (discussed in the section on ‘data and databases in ADMS’). ")]),_c('p',[_vm._v(" ADMS themselves are utilised for the explicit purpose of surveillance within policing and other public systems – to profile individuals and groups, or to identify and predict people’s movements and behaviours and classify them based on their perceived risk and undesirability. As technologies like social media or CCTV cameras allow for the constant production of data about individuals and populations, algorithmic systems are deployed to automatically or programmatically sort and classify such information, at massive scales. Therefore, the act of surveillance itself – what kind of information to acquire, and what kind of behaviour it reveals – becomes automated, continuous and cumulative – bounded by the logic of the particular algorithmic system."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote2sym","name":"sdfootnote2anc"}},[_c('sup',[_vm._v("2")])])])]),_c('p',[_vm._v(" Automated surveillance technologies, like social media surveillance systems and automated facial recognition systems across cities, which are able to collect and process vast amounts of data to draw meaningful inferences about the people being surveilled, are shifting us into a new paradigm of mass surveillance and continuous policing and discipline. In India, departments ranging from state police forces to the Income Tax Department have procured and utilised systems for such unconstrained surveillance, including analysis of interconnected government databases as well as social media surveillance. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-3'),expression:"'case-study-3'"}],staticClass:"modal-button"},[_vm._v("Case Study: Facial Recognition Systems in India")]),_c('b-modal',{attrs:{"id":"case-study-3","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: Facial Recognition Systems in India")]),_c('p',[_vm._v(" One of the primary modalities of automated surveillance in India today is through the use of Facial Recognition Technologies (FRT). FRT uses various algorithmic techniques to extract features of individual faces captured through photographs or video feeds, and compares them with an existing database of faces, in order to identify whether there is a match between the two. ")]),_c('p',[_vm._v(" The use of FRT for policing has grown exponentially in India, in line with the increasing use of video surveillance devices like CCTV cameras, particularly within urban centres and ‘smart cities’. Police agencies in India have claimed to employ FRT for purposes ranging from tracking missing children, to identifying protestors and ‘rowdy elements’. ")]),_c('p',[_vm._v(" Automated Facial Recognition is one of many technologies integrated into ADMS use in policing, which allows for hidden, ubiquitous surveillance. At the time of writing, this toolkit has documented more than 20 implemented or proposed uses of FRT since 2015 alone, each existing without a clear legal basis and without appropriate mechanisms for regulation or oversight. Since 2019, the Government of India has also attempted to create a ‘national’ Automated Facial Recognition System which will attempt to be a centralised FRT system, built upon CCTNS infrastructure, for all state and central policing and intelligence forces to utilise. ")]),_c('p',[_vm._v(" Although the utility and accuracy of these systems is often circumspect, FRT technologies substantially expand the surveillance capabilities of the state. Without any form of regulation or oversight, they are creating the very real possibility of continuous and ubiquitous mass surveillance with very little justification. ")]),_c('p',[_vm._v(" Read more "),_c('a',{attrs:{"href":"https://medium.com/dfrlab/op-ed-the-global-ai-industry-is-intensifying-mass-surveillance-in-india-5c79ebc1bde2"}},[_c('u',[_vm._v("here")])]),_vm._v(" about how Facial Recognition Technologies are intensifying mass surveillance in India. ")])])])],1),_c('p',[_vm._v(" Another way in which ADMS implicate privacy is through the inference and production of information about individuals or groups in ways which were not consensually disclosed, known as automated profiling. Automated profiling often concerns seemingly innocuous information which is aggregated and algorithmically computed in ways which can reveal sensitive information, including preferences, attributes and behaviours about individuals and groups. The information generated through profiling may be inaccurate or incomplete, or, even when accurate, may reveal information contrary to an individual’s agency and self-determination. Further, this inferred information is "),_c('a',{attrs:{"href":"http://privacyinternational.org/report/1718/data-power-profiling-and-automated-decision-making-gdpr"}},[_vm._v("subsequently")]),_vm._v(" used to make "),_c('a',{attrs:{"href":"https://www.law.ox.ac.uk/business-law-blog/blog/2018/10/right-reasonable-inferences-re-thinking-data-protection-law-age-big"}},[_vm._v("decisions")]),_vm._v(" concerning individuals. ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" Automated profiling often concerns seemingly innocuous information which is aggregated and algorithmically computed in ways which can reveal sensitive information, including preferences, attributes and behaviours about individuals and groups. ")]),_c('div',[_c('b-img',{staticStyle:{"width":"100%","height":"650px","object-fit":"cover"},attrs:{"center":"","src":require("../../public/icon1.png"),"fluid":"","alt":"icon 1"}})],1),_c('p',[_vm._v(" Profiling through automated means are now a common feature of our online lives – programmatic and behavioral targeting of advertisements on social media, for "),_c('a',{attrs:{"href":"https://medium.com/swlh/ai-advertising-a-consumer-perspective-f8cd0fb6893"}},[_vm._v("example")]),_vm._v(", attempts to identify attributes about consumer behaviour in order to send relevant advertisements. Perhaps more concerning, some cases of profiling are explicitly used to aid in the political manipulation of the subjects of profiling, for example, the revelations made about the firm Cambridge Analytica attempting to manipulate voter behaviour on Facebook."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote3sym","name":"sdfootnote3anc"}},[_c('sup',[_vm._v("3")])])])]),_c('p',[_vm._v(" The ADMS documented in this project exhibit forms of behavioural profiling at individual and group levels. For example, ‘sentiment analysis’ tools are being used by police departments to trawl through social media and understand responses of communities to different events, including political events like Supreme Court judgements on the Ayodhya land dispute, or the abrogation of Article 370 in Kashmir. Such systems attempt to identify the ‘emotional’ responses of individuals and communities, and provide this information to law enforcement and other government authorities. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-4'),expression:"'case-study-4'"}],staticClass:"modal-button"},[_vm._v("[Case Study: Social Media Surveillance and Profiling in India’s Policing Agencies]")]),_c('b-modal',{attrs:{"id":"case-study-4","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v(" Case Study: Social Media Surveillance and Profiling in India’s Policing Agencies ")]),_c('p',[_vm._v(" The surveillance capabilities of government agencies and law enforcement have been transformed with the advent of programmatic and automated surveillance. The internet, and social media in particular, has emerged as a space for gaining all kinds of insights into the behaviours of groups and individuals, which are used to aid policing and law enforcement. Even though these systems rely largely on information which is "),_c('a',{attrs:{"href":"https://scroll.in/article/893015/40-government-departments-are-using-a-social-media-surveillance-tool-and-little-is-known-of-it"}},[_vm._v("‘open’ and available online")]),_vm._v(", (referred to by law enforcement as Open Source Intelligence or OSINT), these systems jeopardise expectations of privacy because information is used in contexts for which it was not intended."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote4sym","name":"sdfootnote4anc"}},[_c('sup',[_vm._v("4")])])])]),_c('p',[_vm._v(" Social media surveillance systems are widely used in both law enforcement as well as in other government departments. Two major forms of social media surveillance documented in this project including sentiment analysis tools, and social network analysis tools. Sentiment Analysis tools like AASMA scan social media posts for particular words or phrases, and alert agencies when it finds these terms are found, and can also be used to flag what kind of behaviour such speech indicates (‘violent’, ‘anti-national’, ‘anti-social’). Social Network Analysis Tools like ‘"),_c('a',{attrs:{"href":"https://www.x1.com/products/x1-social-discovery/"}},[_vm._v("X1 Social Discovery")]),_vm._v("’ allows law enforcement to automate the process of surveilling particular individuals, and generating profiles of their social networks or ‘associates’. ")]),_c('p',[_vm._v(" Algorithmic surveillance flips presumptions of innocence, and requirements for specificity or justification in targeting individuals or groups for surveillance, by roping every individual and every action into a matrix of probability which is used to decide undesirable or unlawful behaviour. Ultimately, it is transforming the relationship between the state and the citizen – where all citizens are presumed to be located on this scale of probable guilt, and subject to invasive surveillance."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote5sym","name":"sdfootnote5anc"}},[_c('sup',[_vm._v("5")])])])])])])],1),_c('div',{staticClass:"footnotes"},[_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote1anc","name":"sdfootnote1sym"}},[_vm._v("1")]),_vm._v(" Committee of Experts on Non Personal Data Governance Framework, "),_c('em',[_vm._v("supra")])])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote2anc","name":"sdfootnote2sym"}},[_v